body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.badge {
  font-size: 20px;
  width: 250px;
  display: table-cell;
}

.badge-img {
  width: 200px;
  height: 150px;
}

.badge-details {
  padding: 10px;
}

@media screen and (max-width: 680px) {
  .badge {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }
}
#achievements {
  display: -webkit-flex;
  display: flex;
  min-height: 50vh;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.section-profile {
  display: -webkit-flex;
  display: flex;
  height: 50vh;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

  background-image: url(/static/media/quit-background.547de6f3.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;  
}

.form {
  padding: 0 20px;
}

label {
  color: #151515;
}
.App {
  text-align: center;
}

.App-logo {
  height: 65vmin;
  cursor: pointer;
}

.App-header {
  background-color: #151515;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.App-link {
  color: #61dafb;
}
